@media (min-width: 576px) and (max-width: 767.98px) {
  .container {
    width: 540px;
  }

  .hidden {
    display: block;
  }
  //
  .button-burger {
    display: flex;
  }
  .nav__wrapper {
    overflow: hidden;
    display: none;
    //height: 100%;
  }
  .nav__link-list {
    //position: absolute;
    margin: 0 auto;
    //left: 0;
    //right: -1000px;
  }
  .helper {
    //display: none;
    margin-right: 0;
    margin-left: 5px;
  }
  .helper__wrapper {
    left: -160px;
    width: 200px;
    padding: 12px;
  }
  .cash {
    margin-left: auto;
    font-size: 18px;
  }
  .logo__text, .header__email, .header__phone {
    display: none;
  }



  .nav__link-list {
    flex-direction: column;
  }
  .nav__item-list {
    padding-top: 15px;
    padding-bottom: 15px;
    &:first-child {
      margin-top: 15px;
    }
  }
  .header__reg-button {
    margin-bottom: 15px;
    margin-top: 15px;
    display: none;
  }
  .calck__header {
    margin-bottom: 25px;
  }
  .header__enter-link {
    margin-bottom: 30px;
    margin-top: 15px;
    display: none;
  }
  .nav__item-list_metka:before {
    display: none;
  }
  .banner__title {
    font-size: 38px;
  }
  .banner__button {
    margin-bottom: 110px;
  }
  .card {
    width: 330px;
    padding: 15px 20px;
  }
  .card__label {
    width: 100%;
  }

  .calck__group {
    flex-direction: column;
  }
  .calck__input-group {
    width: 100%;
  }
  .calck__memory {
    margin-bottom: 15px;
    flex-wrap: wrap;
  }
  .memory-switcher__type {
    margin-bottom: 15px;
  }
  .calck__flex-wraper {
    margin-bottom: 10px;
  }


  .is-work__list {
    flex-direction: column;
    align-items: center;
  }
  .is-work__list-item {
    margin: 55px;
    &:before {
      transform: rotate(90deg);
      left: 0;
      top: -95px;
    }
    &_4 {
      &:before {
        top: -135px;}
    }
  }

  .news__list {
    flex-wrap: wrap;
  }
  .news__list-item {
    width: calc(100%/2 - 30px);
    margin: 0 15px 15px;
  }

  .footer__wrapper {
    position: relative;
  }

  .footer__logo {
    width: auto;
  }
  .footer__contact {
    display: none;
  }

  .footer__menu {
    position: absolute;
    min-width: 100px;
    max-width: 100%;
    align-items: center;
    justify-content: space-around;
    bottom: -40px;
    left: 0;
    //right: -1500px;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    opacity: 0;
    display: none;
  }
  .footer__menu-link {
    width: 100%;
    text-align: center;
  }
  .edge {
    background: #0089de url(../img/edge-mobile.jpg) center no-repeat;
    padding: 40px 0;
  }
  .edge__wrapper {
    flex-direction: column;
    align-items: center;
  }
  .edge__card {
    &:first-child {
      margin-bottom: 123px;
    }
  }

  .banner {
    padding: 23px 0;
    background-size: auto;
  }
  .calck {
    padding: 25px;
  }
  .card__title {
    font-size: 20px;
  }
  .card__text {
    font-size: 15px;
  }
  .card__label {
    font-size: 15px;
  }
  .card__button {
    font-size: 15px;
    height: 37px;
    width: 148px;
    margin: 0;
  }
  .footer__menu-link {
    padding: 0;
    width: auto;
    border: none;
  }
  .calck-section__title, .customers__title, .quest__title, .news__title, .is-work__title {
    font-size: 24px;
  }
  .calck-section {
    padding: 51px 0;
  }
  .calck-section__title {
    margin-bottom: 33px;
  }
  .calck__title-footer {
    font-size: 23px;
  }
  table {
  }
  tr {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;

  }

  td, th {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: calc(100%/9) !important;
  }

  tbody th:first-child,
  tbody td:first-child {
    text-align: center;
    background: #efefef;
    min-width: 100% !important;
    //position: sticky;
  }
  tbody, thead {
    min-width: 100%;
    display: block;
  }
  tbody {
    tr:last-child {
      th:first-child {
        border-bottom-left-radius: 0 !important;
      }
    }
  }
  thead th:first-child {
    display: none;
  }


  .about__img {
    width: 100%;
  }
  .nav__link-list {
    right: -1000px;
  }
  .reg__title {
    font-size: 24px;
    margin-bottom: 50px;
  }
  .reg {
    padding: 27px 0 55px;
  }
  .news__list {
    order: 2;
  }
  .news__title {
    order: 1;
  }
  .news__all-link {
    order: 3;
    margin-top: 30px;
  }



  .panel {
    flex-direction: column;
    box-shadow: none;
  }
  .panel__menu {
    //display: none;
    border-radius: 0px;
    margin-bottom: 20px;
    position: absolute;
    top: 70px;
    left: -280px;
  }
  .panel-tabel__head {
    display: none;
  }
  .lk__panel {
    border-radius: 0;
  }
  .lk__button-burger {
    margin-bottom: 20px;
  }
  .panel-tabel__head {
    border-top-left-radius: 25px;
    .panel-tabel__id {
      border-top-left-radius: 25px;
      border-left: none;
    }
  }
  .panel-tabel__content {
    flex-direction: column;
    margin-bottom: 20px;
    div {
      width: 100%;
      min-height: 60px;
      border-right: 1px solid #e5e5e5;
      justify-content: flex-start;
      position: relative;
      padding-left: 0;
      padding-top: 0;
      padding-bottom: 0;
      &:before {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 134px;
        min-height: 60px;
        max-height: 100px;
        background: #24ce9f;
        margin-right: 11px;
        color: $white;
        font-size: 15px;
        font-weight: 700;
      }
    }
    .panel-tabel__id {
      border-top: 1px solid #e5e5e5;
      &:before {
        content: 'ID';
      }
    }
    .panel-tabel__name {
      &:before {
        content: ' ';
      }
    }
    .panel-tabel__payment {
      &:before {
        content: ' ';
      }
    }

    .panel-tabel__status {
      &:before {
        content: ' ';
      }
    }
    .panel-tabel__value {
      &:before {
        content: '';
      }
    }
    .panel-tabel__control {
      border-right: 0;
      padding-right: 0;
      &:before {
        content: '';
      }
    }
    &:last-child {
      .panel-tabel__control {
        border-radius: 0;
      }
    }
  }
  .lk__title {
    font-size: 24px;
  }
  .dropdown {
    margin-top: 20px;
    position: static;
    display: none;
  }
}