$gray : #e5e5e5;
$menu-marker : #bababa;
$blue : #009eff;
$white : #ffffff;
$black : #000000;
$blue-dark : #4088da;

@mixin button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  text-decoration: none;
}
@mixin button-main {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  text-decoration: none;
}

@mixin hover-menu {
  color: $blue;
}

@mixin box-shadow-calck {
  box-shadow: 0px 0px 60px 30px rgba(0,0,0,0.15);
}

@mixin box-shadow-helper {
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.15);
}

@mixin hover {
  background: #009eff;
}