@media (min-width: 1200px) and (max-width: 1260px){
  .container {
    width: 1140px;
  }
  table tr th {
    min-width: 99px;

  }
  .nav__item-list {
    &:hover .dropdown {
      display: flex;
    }
  }
}

@media (min-width: 1261px) {
  table tr th {
    min-width: 106px;
  }
  .nav__item-list {
    &:hover .dropdown {
      display: flex;
    }
  }
}
