@media (min-width: 992px) and (max-width: 1199.98px) {
  .container {
    width: 960px;
  }
  .header {

  }

  .logo {
    &__text {
      display: none;
    }
  }

  .edge {
    &__wrapper {
      justify-content: space-between;
    }
  }

  .calck-section {
    &__wrapper {
      padding: 0;
    }
  }
  .calck {
    padding: 50px 25px 32px 25px;
  }
  table tr th:first-child {
    min-width: 88px;
    max-width: 88px;
  }
  .nav__link-list {
    right: -1000px;
  }
  .nav__link {
    padding: 0 25px;
  }
  .calck__header {
    margin-bottom: 30px;
  }
  .nav__item-list {
    &:hover .dropdown {
      display: flex;
    }
  }
}