
@media (max-width: 575.98px) {
  .container {
    width: 287px;
  }

  .hidden {
    display: block;
  }
  //
  .button-burger {
    display: flex;
  }
  .nav__wrapper {
    overflow: hidden;
    display: none;
    //height: 100%;
  }
  .nav__link-list {
    //position: absolute;
    margin: 0 auto;
    //left: 0;
    //right: -1000px;
  }


  .logo__text, .header__email, .header__phone {
    display: none;
  }



  .nav__link-list {
    flex-direction: column;
  }
  .nav__item-list {
    padding-top: 15px;
    padding-bottom: 15px;
    &:first-child {
      margin-top: 15px;
    }
  }
  .header__reg-button {
    margin-bottom: 15px;
    margin-top: 15px;
    display: none;
  }
  .header__enter-link {
    margin-bottom: 30px;
    margin-top: 15px;
    display: none;
  }
  .nav__item-list_metka:before {
    display: none;
  }
  .banner__title {
    font-size: 38px;
  }
  .banner__button {
    margin-bottom: 110px;
  }
  .card {
    width: 100%;
    padding: 15px 20px;
  }
  .card__label {
    width: 100%;
  }

  .calck__group {
    flex-direction: column;
  }
  .calck__input-group {
    width: 100%;
  }
  .calck__memory {
    margin-bottom: 15px;
    flex-wrap: wrap;
    justify-content: center;
  }
  .calck__flex-wraper {
    margin-bottom: 10px;
  }
  .calck__memory-text {
    margin-top: 15px;
    margin-left: 0;
  }

  .is-work__list {
    flex-direction: column;
    align-items: center;
  }
  .is-work__list-item {
    margin: 55px;
    &:before {
      transform: rotate(90deg);
      left: 0;
      top: -95px;
    }
    &_4 {
      &:before {
        top: -135px;}
    }
  }

  .news__list {
    flex-wrap: wrap;
  }
  .news__list-item {
    width: 100%;
    margin: 0 15px 15px;
  }

  .footer__wrapper {
    position: relative;
  }

  .footer__logo {
    width: auto;
  }
  .footer__contact {
    display: none;
  }

  .footer__menu {
    position: absolute;
    min-width: 100px;
    max-width: 100%;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-around;
    bottom: -75px;
    left: 0;
    //right: -1500px;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    opacity: 0;
    display: none;
  }
  .footer__menu-link {
    padding: 0;
    width: auto;
    padding-left: 10px;
    padding-right: 10px;
    border: none;
    margin-bottom: 15px;
  }
  .edge {
    background: #0089de url(../img/edge-mobile.jpg) center no-repeat;
    padding: 40px 0;
  }
  .edge__wrapper {
    flex-direction: column;
    align-items: center;
  }
  .edge__card {
    &:first-child {
      margin-bottom: 123px;
    }
  }

  .banner {
    padding: 23px 0;
    background-size: auto;
  }
  .calck {
    padding: 28px 10px;
  }
  .card__title {
    font-size: 20px;
  }
  .card__text {
    font-size: 15px;
  }
  .card__label {
    font-size: 15px;
  }
  .card__button {
    font-size: 15px;
    height: 37px;
    width: 148px;
    margin: 0;
  }


  .calck-section__title, .customers__title, .quest__title, .news__title, .is-work__title {
    font-size: 24px;
  }
  .calck-section {
    padding: 51px 0;
  }
  .calck-section__title {
    margin-bottom: 33px;
  }
  .calck__title-footer {
    font-size: 23px;
  }
  .calck-section__wrapper {
    padding: 0;
  }
  .calck__input-email, .calck__button , .is-work__button, .banner__button, .banner__input, .banner__button-form{
    width: 221px;
  }

  .time-switcher {
    flex-wrap: wrap;
    height: 100%;
    width: 100%;
    border-radius: 10px;
    &__label {
      width: 79px;
      height: 30px;
    }
    &__polzunok {
      height: 30px;
      border-radius: 10px;
      width: 79px;
    }
    &__input#one-day:checked ~ &__polzunok {
      left: 0px;
    }
    &__input#one-week:checked ~ &__polzunok {
      left: 94px;
    }
    &__input#one-month:checked ~ &__polzunok {
      left: calc(94px*2);
    }
    &__input#tree-month:checked ~ &__polzunok {
      left: 0;
      top: 30px;
    }
    &__input#six-month:checked ~ &__polzunok {
      left: 94px;
      top: 30px;
    }
    &__input#one-year:checked ~ &__polzunok {
      left: calc(94px*2);
      top: 30px;
    }
  }
  .switcher-ram {
    flex-wrap: wrap;
    height: 100%;
    width: calc(79px*3);
    border-radius: 10px;
    justify-content: flex-start;
    margin: 0 auto 15px;
    &__label {
      width: 79px;
      height: 30px;
    }
    &__polzunok {
      height: 30px;
      border-radius: 10px;
      width: 79px;
    }
    &__input#mb512:checked ~ &__polzunok {
      left: 0;
    }
    &__input#gb1:checked ~ &__polzunok {
      left: 79px;
    }
    &__input#gb2:checked ~ &__polzunok {
      left: calc(79px*2);
    }
    &__input#gb3:checked ~ &__polzunok {
      left: 0;
      top: 30px;
    }
    &__input#gb4:checked ~ &__polzunok {
      left: 79px;
      top: 30px;
    }
  }
  .switcher-pro {
    flex-wrap: wrap;
    height: 100%;
    width: calc(79px*3);
    border-radius: 10px;
    justify-content: flex-start;
    margin: 0 auto 15px;
    &__label {
      width: 79px;
      height: 30px;
    }
    &__polzunok {
      height: 30px;
      border-radius: 10px;
      width: 79px;
    }
    &__input#one-i:checked ~ &__polzunok {
      left: 0px;
    }
    &__input#two-i:checked ~ &__polzunok {
      left: 79px;
    }
    &__input#three-i:checked ~ &__polzunok {
      left: calc(79px*2);
    }
    &__input#four-i:checked ~ &__polzunok {
      left: 0;
      top: 30px;
    }
  }
  .memory-switcher__type {
    margin-bottom: 15px;
  }
  .card__label:before {
    left: 3px;
  }
  .copy__link-b {
    flex-direction: column;
  }
  .copy__link {
    padding: 0;
    border: none;
    margin-bottom: 10px;
  }
  .helper {
    //display: none;
    margin-right: 0;
    margin-left: 5px;
  }
  .helper__wrapper {
    left: -160px;
    width: 200px;
    padding: 12px;
  }
  .calck__header {
    //.helper {
    //  margin-right: auto;
    //  margin-left: auto;
    //  margin-top: auto;
    //  margin-bottom: auto;
    //}
  }
  .calck__input-text {
    max-width: 157px;
    display: flex;
    align-items: center;
  }
  //.calck__input-text {
  //  font-size: 18px;
  //}
  table {
    overflow-x: auto;
  }
  tr {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;
  }

  td, th {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 75px !important;
  }

  tbody th:first-child,
  tbody td:first-child {
    text-align: center;
    background: #efefef;
    //min-width: 100% !important;
    //position: sticky;
  }
  //tbody, thead {
  //  min-width: 100%;
  //  display: block;
  //}
  tbody {
    tr:last-child {
      th:first-child {
        border-bottom-left-radius: 0 !important;
      }
    }
  }
  //thead th:first-child {
  //  display: none;
  //}
  .about__img {
    width: 100%;
  }
  .bg-title {
    padding: 0;
    justify-content: center;
  }
  .about__title {
    font-size: 24px;
  }
  .reg__title {
    font-size: 24px;
    margin-bottom: 50px;

  }
  .form-reg__fiz-label span, .form-reg__ur-label span {
    font-size: 12px;
  }
  .reg {
    padding: 27px 0 55px;
  }
  .news__list {
    order: 2;
  }
  .news__title {
    order: 1;
  }
  .news__all-link {
    order: 3;
    margin-top: 30px;
  }
  .cash {
    margin-left: auto;
    font-size: 19px;
    font-family: Consolas, sans-serif;
    font-weight: 400;
    max-width: 44px;
  }
  .calck__label {
    flex-wrap: nowrap;
    justify-content: flex-start;
    &:first-child {
      .helper {
        margin-left: 8px;
      }
    }
    &:nth-child(2) {
      .helper {
        margin-bottom: 18px;
      }
    }
  }
  .help_1 {
    .helper {
      margin-left: 15px;
    }
  }
  .help_2 {
    .helper {
      margin-right: 15px;
    }
  }

  .panel {
    flex-direction: column;
    box-shadow: none;
  }
  .panel__menu {
    //display: none;
    border-radius: 0px;
    margin-bottom: 20px;
    position: absolute;
    top: 70px;
    left: -280px;
  }
  .panel-tabel__head {
    display: none;
  }
  .lk__panel {
    border-radius: 0;
  }
  .lk__button-burger {
    margin-bottom: 20px;
  }
  .panel-tabel__head {
    border-top-left-radius: 25px;
    .panel-tabel__id {
      border-top-left-radius: 25px;
      border-left: none;
    }
  }
  .panel-tabel__content {
    flex-direction: column;
    margin-bottom: 20px;
    div {
      width: 100%;
      min-height: 60px;
      border-right: 1px solid #e5e5e5;
      justify-content: flex-start;
      position: relative;
      padding-left: 144px;
      padding-top: 15px;
      padding-bottom: 15px;

      &:before {
        position: absolute;
        display: flex;
        width: 125px;
        height: 100%;
        align-items: center;
        background: #24ce9f;
        margin-right: 11px;
        padding-left: 8px;
        color: $white;
        font-size: 15px;
        font-weight: 700;
        left: 0;
      }
    }
    .panel-tabel__id {
      border-top: 1px solid #e5e5e5;
      &:before {
        content: 'ID';
      }
    }
    .panel-tabel__name {
      &:before {
        content: ' ';
      }
    }
    .panel-tabel__payment {
      &:before {
        content: ' ';
      }
    }

    .panel-tabel__status {
      &:before {
        content: ' ';
      }
    }
    .panel-tabel__value {
      &:before {
        content: '';
      }
    }
    .panel-tabel__control {
      flex-wrap: wrap;
      justify-content: flex-start;
      padding: 0;
      border-right: none;
      .panel-tabel__control-list{
        flex: 1;
        display: flex;
        flex-wrap: wrap;
      }
      .panel-tabel__control-link {
        margin-right: 7px;
        margin-left: 7px;
      }
      &:before {
        content: '';
      }
    }
    &:last-child {
      .panel-tabel__control {
        border-radius: 0;
      }
    }
  }
  .lk__title {
    font-size: 24px;
  }
  .form-reg__capcha {
    flex-direction: column-reverse;
  }
  .form-reg__input-cap {
    width: 100%;
  }
  .dropdown {
    margin-top: 20px;
    position: static;
    display: none;
  }
  .calck__header {
    margin-bottom: 20px;
  }
  .helper_margin {
    margin-top: 3px;
  }
}