@media (min-width: 768px) and (max-width: 991px) {
  .container {
    width: 730px;
  }

  .hidden {
    display: block;
  }
  //
  .button-burger {
    display: flex;
  }
  .nav__wrapper {
    overflow: hidden;
    display: none;
    //height: 100%;
  }
  .nav__link-list {
    //position: absolute;
    margin: 0 auto;
    //left: 0;
    //right: -1000px;
  }


  .logo__text, .header__email, .header__phone {
    display: none;
  }



  .nav__link-list {
    flex-direction: column;
  }
  .nav__item-list {
    padding-top: 15px;
    padding-bottom: 15px;
    &:first-child {
      margin-top: 15px;
    }
  }
  .header__reg-button {
    margin-bottom: 15px;
    margin-top: 15px;
    display: none;
  }
  .header__enter-link {
    margin-bottom: 30px;
    margin-top: 15px;
    display: none;
  }
  .nav__item-list_metka:before {
    display: none;
  }
  .banner__title {
    font-size: 55px;
  }
  .banner__button {
    margin-bottom: 110px;
  }
  .card {
    width: 330px;
  }
  .card__label {
    width: 100%;
  }

  .calck__group {
    flex-direction: column;
  }
  .calck__input-group {
    width: 100%;
  }
  .calck__memory {
    margin-bottom: 15px;
  }
  .calck__flex-wraper {
    margin-bottom: 10px;
  }


  .is-work__list {
    flex-wrap: wrap;
  }
  .is-work__list-item {
    margin: 0 65px 55px 65px;
    &_1 {
      order: 1;
    }
    &_2 {
      order: 2;
    }
    &_3 {
      order: 4;
      margin-bottom: 0;
      &:before {
        transform: rotate(90deg);
        left: 0;
        top: -95px;
      }
    }
    &_4 {
      order: 3;
      margin-bottom: 0;
      &:before {
        transform: rotate(180deg);
        left: 203px;
      }
    }
  }

  .news__list {
    flex-wrap: wrap;
  }
  .news__list-item {
    width: calc(100%/2 - 30px);
    margin: 0 15px 15px;
  }

  .footer__wrapper {
    position: relative;
  }

  .footer__logo {
    width: auto;
  }
  .footer__contact {
    display: none;
  }

  .footer__menu {
    position: absolute;
    min-width: 100px;
    max-width: 100%;
    align-items: center;
    justify-content: space-around;
    bottom: -40px;
    left: 0;
    //right: -1500px;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    opacity: 0;
    display: none;
  }
  .footer__menu-link {
    width: 100%;
    text-align: center;
  }
  .visable {
    //display: none;
  }
  table {
  }
  tr {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;

  }

  td, th {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: calc(100%/9) !important;
  }

  tbody th:first-child,
  tbody td:first-child {
    text-align: center;
    background: #efefef;
    min-width: 100% !important;
    //position: sticky;
  }
  tbody, thead {
    min-width: 100%;
    display: block;
  }
  tbody {
    tr:last-child {
      th:first-child {
        border-bottom-left-radius: 0 !important;
      }
    }
  }
  thead th:first-child {
    display: none;
  }
  .nav__link-list {
    right: -1000px;
  }
  .helper__wrapper {
    left: -37px;
  }


  .panel {
    flex-direction: column;
    box-shadow: none;
  }
  .panel__menu {
    //display: none;
    border-radius: 25px;
    margin-bottom: 20px;
    position: absolute;
    top: 70px;
    left: -280px;
  }
  .lk__panel {
    border-radius: 0;
  }
  .lk__button-burger {
    margin-bottom: 20px;
  }
  .panel-tabel__head {
    border-top-left-radius: 25px;
    .panel-tabel__id {
      border-top-left-radius: 25px;
      border-left: none;
    }
  }
  .panel-tabel__content {
    &:last-child {
      .panel-tabel__control {
        border-radius: 0;
      }
    }
  }


  .dropdown {
    margin-top: 20px;
    position: static;
    display: none;
  }
}
