body {
  font-family: "Century Gothic", sans-serif;
  color: $black;
  font-weight: 400;
  font-size: 16px;
  background: $white;
  width: 100%;
}

h1 {
  font-family: "Century Gothic", sans-serif;
  font-size: 67px;
  font-weight: 700;
  color: #000;
}

a {
  color: #4088da;
  font-size: 16px;
  font-family: "Century Gothic", sans-serif;
  text-decoration: underline;
  //&:hover {
  //  text-shadow: 2px 1px 2px rgba(150, 150, 150, 1);
  //}
}

.container {
  width: 1200px;
  margin: 0 auto;
}

.header {
  height: 128px;
  display: flex;
  flex-direction: row;
  align-items: center;
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__button-burger {
    display: none;
  }
  &__email, &__phone {
    padding-left: 66px;
    color: #363636;
    text-decoration: none;
    position: relative;
    &:before {
      content: '';
      position: absolute;
      left: 0;
      width: 45px;
      height: 45px;
      top: 0;
      bottom: 0;
      margin-top: auto;
      margin-bottom: auto;
      background: url("../img/sprite.png") no-repeat;
    }
    &:hover {
      @include hover-menu;
    }
  }
  &__email {
    font-size: 18px;
    &:before {
      background-position: 0 0;
    }
  }
  &__phone {
    font-size: 24px;
    &:before {
      background-position: -55px 0;
    }
    &_bold {
      font-weight: 700;
      color: #000;
    }
    &:hover &_bold {
      @include hover-menu;
    }
  }
  &__reg-button {
    @include button;
    background: #009eff;
    width: 150px;
    height: 43px;
    color: $white;
    font-family: Consolas, sans-serif;
    text-decoration: none;
    transition: all 0.2s;
    &:hover {
      background: #4088da;;
    }
  }
  &__enter-link {
    font-size: 18px;
    font-family: Consolas, sans-serif;
    color: #5d666e;
    text-decoration: none;
    padding-left: 31px;
    position: relative;
    &:before {
      content: '';
      background: url(../img/sprite.png) -119px 0 no-repeat;
      width: 24px;
      height: 24px;
      top: 0;
      bottom: 0;
      margin: auto 0;
      left: 0;
      position: absolute;
    }
    &:after {
      position: absolute;
      content: '';
      bottom: -2px;
      right: 0;
      height: 1px;
      width: 37px;
      background: url("../img/sprite.png") -180px -73px no-repeat;
    }
  }
}

.button-burger {
  min-width: 30px;
  max-width: 30px;
  height: 26px;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  position: relative;
  &__line {
    width: 100%;
    height: 5px;
    background: #5d666e;
    border-radius: 25px;
    transition: all 0.3s;
    transition-delay: 0.2s;
    position: absolute;
    &:first-child {
      top: 0;
    }
    &:last-child {
      bottom: 0;
    }
    &:nth-child(2) {
      transition-delay: 0.4s;
      top: 0;
      bottom: 0;
      margin: auto 0;
      opacity: 1;
    }
  }
  &_active {
    .button-burger__line {
      &:first-child {
        transform: rotate(45deg);
        top: 10.5px;
      }
      &:last-child {
        transform: rotate(-45deg);
        bottom: 10.5px;
      }
      &:nth-child(2) {
        opacity: 0;
        transition-delay: 0s;
      }
    }
  }
}


.logo {
  display: flex;
  flex-direction: row;
