.switcher-ram {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 304px;
  height: 31px;
  background: #e9e9e9;
  border-radius: 25px;
  position: relative;
  * {
    transition: all 0.2s;
  }
  &__label {
    cursor: pointer;
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: center;
    width: calc(100%/5);
    position: relative;
    z-index: 2;
    //&:first-child {
    //  padding-left: 10px;
    //}
  }
  &__input {
    display: none;
  }
  &__text {
    font-size: 14px;
  }
  &__polzunok {
    position: absolute;
    width: 60.8px;
    height: 100%;
    border-radius: 25px;
    left: 0;
    background: #24ce9f;
    z-index: 1;
  }
  //
  &__input:checked + &__label>&__text {
    color: $white;
    font-weight: 700;
  }
  &__input#mb512:checked ~ &__polzunok {
    left: 0;
  }
  &__input#gb1:checked ~ &__polzunok {
    left: calc(60.8px);
  }
  &__input#gb2:checked ~ &__polzunok {
    left: calc(60.8px*2);
  }
  &__input#gb3:checked ~ &__polzunok {
    left: calc(60.8px*3);
  }
  &__input#gb4:checked ~ &__polzunok {
    left: calc(60.8px*4);
  }
}

