.memory-switcher {
  display: flex;
  &__value {
    display: flex;
    width: 151px;
    border-radius: 25px;
    background: #24ce9f;
    position: relative;
    height: 32px;
    overflow: hidden;
  }
  &__input {
    cursor: default;
    border: none;
    background: transparent;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: $white;
    font-weight: 700;
    font-size: 14px;
    padding-right: 20px;
  }
  &__minus, &__plus {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $white;
    font-weight: 400;
    cursor: pointer;
    padding: 0 15px;
  }
  &__minus {
    left: 0;
    font-size: 34px;
    margin-bottom: 8px;
  }
  &__plus {
    right: 0;
    font-size: 20px;
  }
  &__gb {
    position: absolute;
    font-size: 14px;
    font-weight: 700;
    color: $white;
    top: 0;
    bottom: 0;
    margin: auto 0;
    display: flex;
    align-items: center;
    justify-content: center;
    left: calc(151px / 2 + 5px);
  }

  &__type {
    display: flex;
    width: 151px;
    height: 32px;
    border-radius: 25px;
    background: #e5e5e5;
