        }
      }
      &_ext {
        &:before {
          background-position: -238px -99px;
        }
      }
    }
  }
  &__content {
    width: 100%;
  }
}

.panel-tabel {
  &__head {
    display: flex;
    width: 100%;
    height: 86px;
    background: #24ce9f;
    border-top-right-radius: 25px;
    color: $white;
    font-weight: 700;
    .panel-tabel__control {
      border-top-right-radius: 25px;
      border-right: none;
      align-items: center;
      justify-content: center;
    }
  }
  &__content {
    display: flex;
    min-height: 112px;
    color: #363636;
    font-size: 16px;
    &:last-child {
      .panel-tabel__control {
        border-bottom-right-radius: 25px;
      }
    }
  }
  &__id {
    width: 9%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-left: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    padding: 0 15px;
  }
  &__name {
    width: 22%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-left: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    padding: 0 15px;
  }
  &__payment {
    width: 22%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-left: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    padding: 0 14px;
  }
  &__status {
    width: 12%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-left: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    padding: 0 15px;
  }
  &__value {
    width: 16%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-left: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    padding: 0 15px;
  }
  &__control-list{
    flex: 1;
    display: flex;
    flex-wrap: wrap;
  }
  &__control {
    width: 19%;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-left: 1px solid #e5e5e5;
    border-right: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    padding: 11px 10px;
    flex-wrap: wrap;
  }
  &__control-link {
    width: 32px;
    height: 32px;
    background: #fff url(../img/sprite.png) no-repeat;
    margin-left: 9px;
    margin-right: 9px;
    margin-bottom: 9px;
    margin-top: 9px;
    &_tiket {
      background-position: 0 -145px;
    }
    &_edit {
      background-position: -52px -145px;
    }
    &_credit {
      background-position: -111px -145px;
    }
    &_messange {
      background-position: -174px -145px;
    }
    &_del {
      background-position: -229px -145px;
    }
  }
}

.lk__button-burger {
  position: relative;
  &:after {
    content: ' ';
    font-size: 18px;
    height: 100%;
    top: 0;
    left: 70px;
    position: absolute;
    font-weight: 700;
    min-width: 155px;
    display: flex;
    align-items: center;
    padding-bottom: 10px;
    box-sizing: border-box;
  }
  &.button-burger {
    background: #92a2b0;
    padding: 11px 14px;
    border-radius: 10px;
    min-width: 58px;
    height: 51px;
    .button-burger__line {
      width: 29px;
      background: $white;
      left: 14px;
      &:first-child {
        top: 11px;
      }
      &:last-child {
        bottom: 11px;
      }
    }
    &.button-burger_active .button-burger__line:first-child {
      top: 23px;
    }
    &.button-burger_active .button-burger__line:last-child {
      bottom: 23px;
    }
  }
}

